@import "../../style.scss";

.post {
  @include themify($themes) {
    -webkit-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    -moz-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    border-radius: 20px;
    background-color: themed("bg");
    color: themed("textColor");

    .container {
      padding: 20px;

      .user {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .userInfo {
          display: flex;
          gap: 20px;

          img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
            cursor: pointer;

          }

          .details {
            display: flex;
            flex-direction: column;
            .name {
              font-weight: 500;
            }
            .date {
              font-size: 12px;
            }
          }
        }
      }

      .content {
        margin: 20px 0px;

        img {
          width: 100%;
          height: 500px;
          max-height: 500px;
          border-radius: 0%;
          margin-top: 20px;
        }
        .showmore {
          padding: 6px 3px;
          text-align: center;
          text-decoration: none;
          display: inline-block;
          font-size: 10px;
          margin: 4px 2px;
          transition-duration: 0.4s;
          cursor: pointer;
          background-color: white;
          color: black;
          border: 2px  #f44336;
        }
        .showmore:hover {
          background-color: #1a382c;
          color: white;
        }
      }

      .info {
        display: flex;
        align-items: center;
        gap: 20px;
        .item {
          display: flex;
          align-items: center;
          gap: 10px;
          cursor: pointer;
          font-size: 14px;
        }
        .flagItem{
          display: flex;
          justify-content: flex-end;
          cursor: pointer;
          font-size: 14px;
          
          
        }
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .flagItem {
    padding-left: 520px;
  }
}

@media screen and (max-width: 767px) {
  .flagItem {
    padding-left: 20px;
  }
}
